@import url(https://fonts.googleapis.com/css?family=Gelasio:700|Open+Sans:400,600&display=swap);
html,body{max-width:680px;color:#02241F;padding:0px 20px;margin:0px auto;box-sizing:border-box;outline:0;height:100%;font-family:"Open Sans",sans-serif;font-size:16px}h1,h2,h3,h4,h5,h6{font-family:"Gelasio",serif}ul,li,p{margin:0;padding:0}a{text-decoration:none;color:#02241F}a:hover{color:#FF6060;border-radius:4px;border-bottom:4px solid #FF6060;-webkit-transition:all;transition:all}.container{margin:30px auto 60px}.navbar{display:flex;align-items:baseline;justify-content:space-between;padding:30px 0px;border-bottom:1px solid #FFD853}.navbar a{font-size:26px;font-family:"Gelasio",serif}@media (min-width: 576px){.navbar a{font-size:36px}}.navbar a:hover{border-radius:none;border-bottom:none}.social{margin-top:10px;margin-bottom:10px;font-size:12px;font-weight:600;text-transform:uppercase;line-height:1;letter-spacing:.1em;-webkit-transition:opacity .3s ease-in-out;transition:opacity .3s ease-in-out}@media (min-width: 576px){.social{text-align:right}}.social .social_items ul{display:inline-block}.social .social_items ul li{display:inline-block;margin-right:10px}.now{padding-bottom:50px}.now_section h1{font-size:24px}.now_section h3{font-size:20px}.now_section a{color:#FF6060}.now_section a:hover{border-radius:4px;border-bottom:4px solid #FF6060}.now_section:last-of-type{margin-top:40px}.now_section.reads{display:block}@media (min-width: 576px){.now_section.reads{display:flex;flex:1 1}}@media (min-width: 576px){.now_section_reads{flex:1 1}}.now_section_reads ul li{list-style-type:none}

